






























import {requestip} from '../api/api.js'
export default{
	data(){
		return{
			data:'',
			Tabindex:0,
			rightData:'',
			datas:[],
			src:'../../static/search.png',
			gooDid:1,
			leftH:0
		}
	},
	methods:{
		Nato(){
			uni.navigateTo({
				url:'../searchinput/searchinput'
			})
		},
		// 左边点击
		tabindexs(i,n){
			this.Tabindex=n;
			// 点击数据
			this.gooDid=i.id;
			
		},
		changeTab(e){
			this.Tabindex=e.detail.current
		},
		// 右边点击
		gooDitems(i){
			uni.navigateTo({
				url:'../gooList/gooList?id='+i.opt_id+'&num=0&name='+i.name
			})
		},
		// 查看全部
		looKall(){
			var allId=this.gooDid;
			uni.navigateTo({
				url:'../gooList/gooList?id='+allId+'&num=1&name=查看全部'
			})
		}
		
			
	},
	onLoad() {
		uni.hideShareMenu();
		this.gooDid=1;
		uni.showLoading({
			title:"加载中..."
		})
		//  初始化数据
		uni.request({
			url: requestip()+'/product/index/class',
			method: 'GET',
			header:{
				"access-token":uni.getStorageSync('access-token'),
			    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
			   },
			data: {},
			success: res => {
				// console.log("初始化数据：",res.data);
				this.data=res.data;
				this.datas=res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
		var that=this;
		uni.getSystemInfo({
			success(res){
				console.log('设备信息',res);
				that.leftH=res.windowHeight;
			}
		})
	}
}
